import React, { useContext, useEffect, useState } from "react";
import BlankCard from "../../UI/Card/BlankCard";
import ContentCard from "../../UI/Card/ContentCard";
import ForcedResetPasswordForm from "./ForcedResetPasswordForm";
import ThemeContext from "../../../store/theme-context";
import SuccessReset from "./SuccessReset";
import { getForcedResetStatus } from "../../utils/auth";

function ForcedResetPassword() {
  const themeCtx = useContext(ThemeContext);
  const [isResetted, setIsResetted] = useState(false);
 
  const storedIsResetted = getForcedResetStatus();
  useEffect(() => {
    if (storedIsResetted === "1") {
      setIsResetted(true);
    }
  }, [storedIsResetted]);
  
  //console.log("is resetted: " + storedIsResetted);

  return (
    <div className={`main_content`}>
      <ContentCard
        className={` ${
          themeCtx.darkmode ? "dark_bg" : "white_bg"
        } resetPwd_form_container`}
      >
        <BlankCard>
          {isResetted && (<SuccessReset />)}
          {!isResetted && <ForcedResetPasswordForm />}
        </BlankCard>
      </ContentCard>
    </div>
  );
}

export default ForcedResetPassword;