import React from "react";
import BlankCard from "../../UI/Card/BlankCard";

import JobItem from "./JobItem";


function JobsList(props) {
    if (props.items === null) {
        return <h4 className="text-center my-3">No Jobs found</h4>;
    }
    return (
      <BlankCard>
        {props.items.length > 0 && (
          <ul className=" jobsList">
            <li className="job_item_row job_row_header">
              <div className="job_column job_id">ID</div>
              <div className="job_column job_status">Status</div>
              <div className="job_column job_submitted ">Submitted</div>
              <div className="job_column job_note">Note</div>
            </li>

            {props.items.map((job) => (
              <JobItem
                key={job.id}
                id={job.id}
                status={job.status}
                submitted={job.submitted}
                note={job.note}
              />
            ))}
          </ul>
        )}
      </BlankCard>
    );
}

export default JobsList;
